
@import url('https://fonts.googleapis.com/css2?family=SUSE:wght@100..800&display=swap');

:root{
  --c1: rgb(53, 47, 68);
  --c2: rgb(92, 84, 112);
  --c3: rgb(185, 180, 199);
  --c4: rgb(250, 240, 230);

  background-color: rgb(25, 25, 25);
}

html{
  background-color: rgb(25, 25, 25);
}

*{
  padding: 0px;
  margin: 0px;
  border: 0px;

  font-family: 'SUSE';
  user-select: none;
}

.page{
  width: 100%;
  min-height: 100vh;

  padding: 50px 175px;

  background-color: rgb(25, 25, 25);

  box-sizing: border-box;
  display: flex;
  flex-direction: column;

  border-bottom: 1px solid rgba(255, 255, 255, 0);
}

h1{
  font-size: 90px;
  font-weight: 600;
}

h2{
  font-size: 15px;
  font-weight: 500;

  color: rgba(250, 240, 230, 0.75);
}

h3{
  font-size: 25px;
  font-weight: 700;
}

h4{
  font-size: 20px;
  font-weight: 300;

  color: rgba(250, 240, 230, 0.75);
}

h5{
  font-size: 20px;
  font-weight: 600;

  color: var(--c3);

  text-transform: uppercase;
}

h6{
  font-size: 45px;
  font-weight: 600;

  color: var(--c4);
}

p{
  font-size: 16px;
  font-weight: 300;

  line-height: 30px;

  color: rgba(250, 240, 230, 0.7);
}

span{
  color: var(--c4);
  font-weight: 400;
  font-size: 20px;
}

@media only screen and (max-width: 600px) {

  h1{
    font-size: 10vw;
    font-weight: 600;
  }

  h3{
    font-size: 15px;
    font-weight: 700;
  }
  
  h4{
    font-size: 15px;
    font-weight: 300;
  
    color: rgba(250, 240, 230, 0.75);
  }  
  
  h6{
    font-size: 40px;
    font-weight: 600;
  }

  .page{
    width: 100%;
    min-height: min-content;
  
    padding: 50px 25px;
  
    background-color: rgb(25, 25, 25);
  
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    
  
    border-bottom: 1px solid rgba(255, 255, 255, 0);
  }
}
