#app{

}

#hero{
  width: 100%;
  height: 100vh;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;

  gap: 10px;

  background-image: 
  linear-gradient(rgba(0, 0, 0, .5), 
                  var(--c1) 80%), url(../public/img/code.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  box-sizing: border-box;

  color: var(--c4);
  text-transform: uppercase;
}

#hero h1{
  letter-spacing: 10px;
  width: max-content;
  text-align: center;
}

#hero ul{
  min-width: 100%;

  display: flex;
  list-style: none;
  justify-content: space-between;
  align-items: center;

  padding-top: 50px;
  padding-bottom: 50px;

  gap: 5px;
}

#hero li{
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;

  text-align: center;
}

#hero li section{
  display: flex;
  flex-direction: column;
  align-items: center;
}

#hero i{
  font-size: 65px;
}

#interested{
  text-decoration: none;
  color:  var(--c4);

  background-color:  var(--c2);
  padding: 15px 75px;

  text-transform: capitalize;

  transition: .25s;
}

#interested:hover{
  transform: translateY(-2px);
}

#why{
  display: flex;
  flex-direction: row;

  flex-wrap: wrap;

  gap: 25px;
}

#why div{
  flex: 1;
  min-width: 500px;
}

#why div:nth-child(1){
  display: flex;
  flex-direction: column;
}

#why div:nth-child(2){
  background-image: url(../public/img/code.png);
  background-size: cover;

  min-height: 50vh;
}

#why p{
  padding: 50px 0px;
}

#why ul{
  display: flex;
  flex-wrap: wrap;
  gap: 25px
}

#why i{
  color: var(--c3);
  display: flex;
  align-items: center;
  gap: 10px;
}

#why a{
  text-decoration: none;
  color: var(--c4);

  background-color: var(--c2);

  padding: 15px 75px;
  width: max-content;
  margin-top: 50px;

  border-radius: 1px;

  text-transform: capitalize;
}

#us{
  display: flex;
  align-items: center;
  
}

#us h5{
  text-align: center;
}

#us h6{
  text-align: center;
}

#us div{
  width: 100%;
  display: flex;

  gap: 50px;

  padding-top: 70px;
  box-sizing: border-box;
}

#us div ul{
  flex: 1;
  gap: 50px;
  display: flex;
  flex-direction: column;
}

#us li{
  display: flex;
  flex-direction: column;
  gap: 10px;
}

#us i{
  background-color: var(--c2);
  color: var(--c4);
  width: min-content;
  padding: 25px;
}

#us div div{
  background-image: url(../public/img/cde.png);
  background-size: contain;
  width: 33%;
  min-height: 100%;
}

#services{
  display: flex;
  align-items: center;

  text-align: center;
}

#services ul{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  gap: 20px;

  padding-top: 70px;
}

#services li{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  min-width: 20vw;

  background-color: var(--c2);

  padding: 30px 20px;
  box-sizing: border-box;

  height: max-content;

  flex: 1;

  gap: 15px;

  text-align: center;
}

#services li section{
  background-color: var(--c1);
  padding: 25px;
  border-radius: 5px;

  margin-bottom: 20px;
  margin-top: 20px;

  transform: rotate(45deg);
}

#services li section i{
  color: var(--c4);

  transform: rotate(-45deg);
}


#services .customCard{
  background-color: var(--c1);

  display: flex;
}

#services .customCard section{
  background-color: var(--c2);
  padding: 25px;
  border-radius: 5px;

  transform: rotate(45deg);
}

#services .customCard section i{
  color: var(--c4);
  transform: rotate(-45deg);
}

#services .customCard article{
  color: var(--c4);

  background-color: var(--c2);

  padding: 10px 15px;

  text-transform: uppercase;
  font-weight: 500;
}


#pricing{
  display: flex;
  align-items: center;
}

#pricing ul{
  list-style: none;
  
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  padding-top: 70px;
  
  width: 100%;

  gap: 10px;
}

#pricing ul li{
  background-color: var(--c2);

  max-width: 100vw;
  min-width: max-content;
  flex: 1;
}

#pricing ul li:nth-child(2){
  background-color: var(--c1);

  box-shadow: 0px 0px 100px rgb(0, 0, 0);

  position: relative;
}

#pricing ul li:nth-child(2) a{
  background-color: var(--c2);
}

#pricing div{
  padding: 25px 50px;

  display: flex;
  flex-direction: column;
}

#pricing div:nth-child(1){
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}

#pricing div:nth-child(2){
  gap: 15px;
}

#pricing ul h6{
  display: flex;
  align-items: baseline;
  text-transform: capitalize;

  transform: translate(10px);
}

#pricing ul h6:after{
  content: '/Month';
  font-size: 20px;

  font-weight: 300;
}

#pricing ul h6:before{
  content: 'â‚¬';
  font-size: 20px;
  position: absolute;

  transform: translate(-100%, 25%);
}

#pricing p{
  width: 100%;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

#pricing i{
  font-size: 20px;
}

#pricing-tick{

}

#pricing-cross{
  color: rgb(221, 35, 35);
}

#pricing a{
  text-decoration: none;
  color: var(--c4);
  background-color: var(--c1);
  text-align: center;

  width: max-content;
  padding: 10px 35px;
  text-transform: capitalize;

  margin-top: 15px;
}

#request{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 50px;
}

#request div{
  flex: 1;
}

#request div:nth-child(1){
  min-width: 40vw;
}

#request div:nth-child(2){
  background-color: var(--c2);

  display: flex;
  flex-direction: column;

  gap: 20px;

  padding: 50px;
  box-sizing: border-box;

  height: min-content;

}

#request div input{
  padding: 15px;

  background-color: rgba(255, 255, 255, 0.75);

  text-transform: capitalize;
  font-weight: 600;
  font-size: 17px;
}

#request div button{
  padding: 15px 75px;

  color: white;
  text-transform: capitalize;

  font-size: 15px;

  background-color: var(--c1);

  cursor: pointer;
}

#request ul{
  display: flex;
  gap: 50px;
  padding: 50px 0px;
}

#request i{
  color: var(--c3);
  display: flex;
  gap: 10px;
  align-items: center;
  font-size: 20px;
}

@media only screen and (max-width: 600px) {

  #hero{
    justify-content: center;
    min-height: max-content;
    padding: 50px 0px;
  }

  #hero ul{
    min-width: min-content;
  
    display: flex;
    list-style: none;
    justify-content: space-around;
    align-items: center;
  
    padding-top: 75px; 
    padding-bottom: 0px;
  
    gap: 5px;

    flex-wrap: wrap;
  }
  
  #hero li{
    min-width: max-content;
    width: calc(100%/3);

    display: flex;
    flex-direction: column;
    align-items: center;
  
    text-align: center;
  }
  
  #hero li section{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  #hero i{
    font-size: 45px;
  }

    
  #why{
    display: flex;
    flex-direction: row;

    flex-wrap: wrap;

    gap: 25px;
  }

  #why div{
    flex: 1;
    min-width: 50vw;
  }

  #why div:nth-child(1){
    display: flex;
    flex-direction: column;
  }

  #why div:nth-child(2){
    background-image: url(../public/img/code.png);
    background-size: cover;

    min-height: 50vh;
  }

  #us div{
    width: 100%;
    display: flex;
    flex-direction: column;
  
    gap: 20px;
  
    padding-top: 70px;
    box-sizing: border-box;
  }
  
  #us div ul{
    flex: 1;
    gap: 20px;
    display: flex;
    flex-direction: row;
  }
  
  #us li{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 10px;
  }

  #us div div{
    background-image: url(../public/img/cde.png);
    background-size: contain;
    width: 100%;
    min-height: 50vh;
  }

  #services{
    padding: 50px 5px;
  }

  #services ul{
    display: flex;
    flex-wrap: wrap;
    flex-wrap: wrap;
    justify-content: center;

    gap: 10px;

    padding-top: 70px;
  }

  #services li{
    flex: 1;

    min-width: 40vw;

    background-color: var(--c2);

    padding: 20px 0px;
    box-sizing: border-box;

    gap: 15px;
  }

  #services li:nth-child(3),  #services li:nth-child(4){
    display: none;
  }

  #services .customCard article{
    color: var(--c4);
  
    background-color: var(--c2);
  
    padding: 10px 15px;
  
    text-transform: uppercase;
    font-weight: 500;
  }
  
  #pricing h6{
    text-align: center;
  }

  #request{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 50px;
  }
  
  #request div{
    width: 100%;
  }
  
  #request div:nth-child(2){
    background-color: var(--c2);
  
    display: flex;
    flex-direction: column;
  
    gap: 20px;
  
    padding: 25px;
    box-sizing: border-box;
  
    max-width: 100%;
    height: min-content;
  }
  
  #request ul{
    flex-direction: column;
    gap: 25px;
    padding: 25px 0px;
  }
}