#navbar{
    top: 0px;
    position: fixed;
    z-index: 9999;

    display: flex;

    justify-content: space-between;
    align-items: center;

    width: 100%;

    background-color: var(--c1);
    color: var(--c4);

    box-sizing: border-box;
    padding: 15px 75px;
}

#navbar section{
    display: flex;
    justify-content: center;
    gap: 50px;
}

#navbar section a{
    color: var(--c4);
    text-decoration: none;
}

#navbar section i{
    display: flex;
    gap: 10px;
}

#navbar section p{
    font-weight: 300;
    line-height: 15px;
    font-size: 16px;
}

#navbar-btn{
    position: fixed;
    top: 0;
    left: 0;

    background-color: var(--c1);
    border: 1px solid var(--c2);

    border-radius: 5px;
    
    padding: 25px;

    margin: 10px;
    
}

@media only screen and (min-width: 600px) {
    #navbar-btn{
        display: none;
    }
}

@media only screen and (max-width: 600px) {
    #navbar section{
        display: none;
    }

    #navbar{
        background-color: transparent;
    }
}